import * as React from 'react';
import { SyntheticEvent } from "react";

interface NotificationFormProps {
  createBreathHoldNotification: (notification: number) => void
  createBreathingNotification: (notification: number) => void
}

export interface NotificationFormState {
  notificationTime: number
}
const defaultState = {
  notificationTime: 30
};

export class AddNotificationForm extends React.Component<NotificationFormProps, NotificationFormState> {
  constructor(props: NotificationFormProps, state: NotificationFormState) {
    super(props, state);
    this.state = defaultState
  }

  public render() {
    return (
      <>
        <div>
            <label htmlFor="notificationTime">Notification on time remaining in seconds: </label>
            <input type="number" name="breathingDecrement" value={this.state.notificationTime} onChange={this.changeNotificationTime}/>
        </div>
        <div>
          <button onClick={this.submitBreathHold}>Create Breath Holding Notification</button>
          <button onClick={this.submitBreathing}>Create Breathing Notification</button>
        </div>
      </>
    );
  }
  private submitBreathHold = (event: SyntheticEvent) => {
    event.preventDefault();
    this.props.createBreathHoldNotification(this.state.notificationTime)
  };

  private submitBreathing = (event: SyntheticEvent) => {
    event.preventDefault();
    this.props.createBreathingNotification(this.state.notificationTime)
  };

  private changeNotificationTime = (event: any) => {
    this.setState({...this.state, notificationTime: parseInt(event.target.value) });
  };
}
