export type EitherTag = "Left" | "Right";

export class Left<L, R> {
  static tag: EitherTag = "Left";

  public value: L;
  public tag: String = Left.tag;

  constructor(value: L) {
    this.value = value;
  }

  public map<T>(f: (value: R) => T): Either<L, T> {
    return new Left<L, T>(this.value);
  }

  public flatMap<T>(f: (value: R) => Either<L, T>): Either<L, T> {
    return new Left<L, T>(this.value);
  }

  public leftMap<T>(f: (value: L) => T): Either<T, R> {
    return new Left<T, R>(f(this.value));
  }

  public fold<T>(lf: (value: L) => T, rf: (value: R) => T): T {
    return lf(this.value);
  }
}

export class Right<L, R> {
  static tag: EitherTag = "Right";
  public value: R;
  public tag: String = Right.tag;

  constructor(value: R) {
    this.value = value;
  }

  public map<T>(f: (value: R) => T): Either<L, T> {
    return new Right(f(this.value));
  }

  public flatMap<T>(f: (value: R) => Either<L, T>): Either<L, T> {
    return f(this.value);
  }

  public leftMap<T>(f: (value: L) => T): Either<T, R> {
    return new Right<T, R>(this.value);
  }

  public fold<T>(lf: (value: L) => T, rf: (value: R) => T): T {
    return rf(this.value);
  }
}

export type Either<L, R> = Left<L, R> | Right<L, R>
