import { TimeTablesSettings } from "./TimeTablesForm";
import { Either, Left, Right } from "../functional-helpers/Either";


export const BreathingSessionType = "Breathing";
export const BreathHoldSessionType = "BreathHold";
export const MinimumSessionTime = 15;

export type TimeTableSessionType = "Breathing" | "BreathHold";
export interface TimeInterval {
  sessionType: TimeTableSessionType
  totalTime: number
  notifications: number[]
}

export interface GenerationFailure {
  message: string
}
export const InvalidBreathHoldStart: GenerationFailure = { message: `Can't have less than ${MinimumSessionTime} seconds breath hold.` };
export const InvalidBreathingStartTime: GenerationFailure = { message: `Can't have less than ${MinimumSessionTime} seconds breathing start time.` };


function generateTimeTables(settings: TimeTablesSettings): Either<GenerationFailure, TimeInterval[]> {
  if (settings.breathHoldStartTime < MinimumSessionTime) {
    return new Left(InvalidBreathHoldStart);
  } else  if (settings.breathingStartTime < MinimumSessionTime) {
    return new Left(InvalidBreathingStartTime);
  } else {
    return new Right(calculateTimeTables(settings));
  }
}

function valueOrMinimum(v: number) {
  return Math.max(v, MinimumSessionTime)
}

function calculateTimeTables(settings: TimeTablesSettings, currentInterval: TimeInterval[] = []): TimeInterval[] {
  if(settings.numberOfReps <= 0) {
    return currentInterval;
  } else {
    const newIntervals: TimeInterval[] = [
      { sessionType: BreathingSessionType, totalTime: settings.breathingStartTime, notifications: settings.breathingNotifications },
      { sessionType: BreathHoldSessionType, totalTime: settings.breathHoldStartTime, notifications: settings.breathHoldNotifications }
    ];
    let breathingStartTime = valueOrMinimum(settings.breathingStartTime - settings.breathingDecrement);
    let breathHoldStartTime = valueOrMinimum(settings.breathHoldStartTime + settings.breathHoldIncrement);
    const nextSession = {
      breathingStartTime,
      breathHoldStartTime,
      numberOfReps: settings.numberOfReps - 1
    };

    return calculateTimeTables({...settings, ...nextSession}, [...currentInterval, ...newIntervals])
  }
}
export default generateTimeTables;
