import * as React from "react";
import { BreathingSessionType, TimeInterval } from "./TimeTablesGenerator";
import { notifyUser } from "./notifications/NotifyUser";

type TimedSessionPresenterProps = { timeInterval: TimeInterval, onComplete: () => void };
type TimedSessionPresenterState = { timeLeft: number };

export class TimedSessionPresenter extends React.Component<TimedSessionPresenterProps, TimedSessionPresenterState> {
  constructor(props: TimedSessionPresenterProps, state: TimedSessionPresenterState) {
    super(props, state);
    this.state = {timeLeft: props.timeInterval.totalTime};
  }

  private onChange = (timeLeft: number) => {
    const notification = this.props.timeInterval.notifications.find(n => n === timeLeft);
    if (notification) {
      notifyUser(`${notification} seconds remaining`);
    }
    this.setState({...this.state, timeLeft})
  };

  public componentDidMount() {
    this.counter(this.state.timeLeft);
  }

  public componentDidUpdate(prevProps: TimedSessionPresenterProps, prevState: TimedSessionPresenterState, snapshot?: any): void {
    if (prevProps.timeInterval === this.props.timeInterval) {
      this.counter(this.state.timeLeft);
    } else {
      this.setState({...this.state, timeLeft: this.props.timeInterval.totalTime})
    }
  }

  private counter(timeLeft: number) {
    if (timeLeft > 0) {
      setTimeout(() => {
        this.onChange(timeLeft - 1);
      }, 1000);
    } else {
      this.onComplete();
    }
  }

  public render() {
    const sessionType = this.props.timeInterval.sessionType;
    const message = sessionType === BreathingSessionType ? "Breathe Normally" : "Hold Your Breath!";
    return (
      <>
        <h2>{message}</h2>
        <h1>{this.state.timeLeft}</h1>
      </>
    )
  }

  private onComplete = this.props.onComplete
}
