import * as React from "react";
import { TimedSessionPresenter } from "./TimedSessionPresenter";
import { BreathingSessionType, TimeInterval } from "./TimeTablesGenerator";
import { notifyUser } from "./notifications/NotifyUser";

type StartPracticeProps = { timeIntervals: TimeInterval[], onCancel: () => void }
type StartPracticeState = { timeIntervalsLeft: TimeInterval[] }

export class StartPractice extends React.Component<StartPracticeProps, StartPracticeState> {
  constructor(props: StartPracticeProps, state: StartPracticeState) {
    super(props, state);
    this.state = {timeIntervalsLeft: props.timeIntervals};
  }

  public render() {
    if (this.state.timeIntervalsLeft.length === 0) {
      notifyUser("Practice has ended");
      return <h1>Practice has ended</h1>;
    } else {
      const interval = this.state.timeIntervalsLeft[0];
      if (interval.sessionType === BreathingSessionType) {
        notifyUser('Breathe normally!');
      } else {
        notifyUser('Start Holding Breath!')
      }
      return <TimedSessionPresenter timeInterval={interval} onComplete={this.nextInterval}/>;
    }
  }

  private nextInterval = () => {
    const timeIntervalsLeft = this.state.timeIntervalsLeft.slice(1);
    this.setState({...this.state, timeIntervalsLeft})
  }
}
