import * as React from 'react';
import TimeTablesForm, { defaultSettings, TimeTablesSettings } from "./TimeTablesForm";
import TimeTablesPractice from "./TimeTablesPractice";

interface TimeTablesState {
  displayTraining: boolean
  timeTablesSettings: TimeTablesSettings
}

const defaultState = {
  displayTraining: false,
  timeTablesSettings: defaultSettings
};


class TimeTables extends React.Component<any, TimeTablesState> {
  constructor(props: any, state: TimeTablesState) {
    super(props, state);
    this.state = defaultState
  }

  public render() {
    if (this.state.displayTraining) {
      return <TimeTablesPractice settings={this.state.timeTablesSettings} onCancelPractice={this.cancelPractice}/>
    } else {
      return (
        <div>
          <TimeTablesForm onSubmit={this.onSettingsUpdate}/>
        </div>
      );
    }
  }

  private cancelPractice = () => {
    this.setState({ ...this.state, displayTraining: false });
  };

  private onSettingsUpdate = (newState: TimeTablesSettings) => {
    this.setState({ ...this.state, timeTablesSettings: newState, displayTraining: true });
  }
}

export default TimeTables;
