import * as React from "react";

import { TimeInterval } from "./TimeTablesGenerator";

type PracticeSummarySettings = { timeIntervals: TimeInterval[], onStart: () => void};

export const PracticeSummary = ({timeIntervals, onStart}: PracticeSummarySettings) => {
  const totalTimeSeconds = timeIntervals.reduce((acc, value) => acc + value.totalTime, 0);
  const totalMinutes = Math.floor(totalTimeSeconds / 60);
  const totalSeconds = totalTimeSeconds % 60;
  return (
    <>
      <h2>Great Success</h2>
      <p>Practice Total Time: {totalMinutes}:{totalSeconds} </p>
      {timeIntervals.map(({sessionType, totalTime}, index) =>
        (
          <div key={index}>
            <p>Session: {sessionType}, totalTime: {totalTime}</p>
          </div>
        )
      )}
      <button className="start-training-button" onClick={() => onStart()}>Start Practice</button>
    </>
  )
};
