import * as React from 'react';
import { SyntheticEvent } from "react";
import { AddNotificationForm } from "./AddNotificationForm";
import { MinimumSessionTime } from "./TimeTablesGenerator";

type Seconds = number

export type NotificationSettings = number

export interface TimeTablesSettings {
  breathHoldStartTime: Seconds;
  breathingStartTime: Seconds;
  breathHoldIncrement: Seconds;
  breathingDecrement: Seconds;
  numberOfReps: number;
  breathHoldNotifications: NotificationSettings[];
  breathingNotifications: NotificationSettings[];
}

interface TimeTablesFormProps {
  onSubmit: (state: TimeTablesSettings) => void;
}

export const defaultSettings = {
  breathHoldStartTime: 90,
  breathHoldIncrement: 0,
  breathingStartTime: 150,
  breathingDecrement: 15,
  numberOfReps: 8,
  breathHoldNotifications: [],
  breathingNotifications: []
};

export const baseNotifications = [
  30, 10, 5
];

class TimeTablesForm extends React.Component<TimeTablesFormProps, TimeTablesSettings> {
  constructor(props: TimeTablesFormProps, state: TimeTablesSettings) {
    super(props, state);
    this.state = defaultSettings;
  }

  public render() {
    return (
      <>
        <form>
          <div>
            <h3>Sessions</h3>
            <div>
              <label htmlFor="breathHoldStartTime">Breath Hold Start Time: </label>
              <input type="number" min={MinimumSessionTime} name="breathHoldStartTime" value={this.state.breathHoldStartTime} onChange={this.changeBreathHoldStartTime}/>
            </div>

            <div>
              <label htmlFor="breathHoldIncrement">Breath Hold Increment: </label>
              <input type="number" min={MinimumSessionTime} name="breathHoldIncrement" value={this.state.breathHoldIncrement} onChange={this.changeBreathHoldIncrement}/>
            </div>
            <br/>

            <div>
              <label htmlFor="breathingStartTime">Breathing Start Time: </label>
              <input type="number" name="breathingStartTime" value={this.state.breathingStartTime} onChange={this.changeBreathingStartTime}/>
            </div>

            <div>
              <label htmlFor="breathingDecrement">Breathing Decrement: </label>
              <input type="number" name="breathingDecrement" value={this.state.breathingDecrement} onChange={this.changeBreathingDecrement}/>
            </div>
            <br/>

            <div>
              <label htmlFor="numberOfReps">NumberOfReps: </label>
              <input type="number" name="numberOfReps" value={this.state.numberOfReps} onChange={this.changeNumberOfReps}/>
            </div>
          </div>
          <div>
            <h3>Notifications</h3>
            <div>
              <AddNotificationForm
                createBreathHoldNotification={this.addBreathHoldNotification}
                createBreathingNotification={this.addBreathingNotification}
              />
            </div>
            <div>
              <button onClick={this.createBaseNotifications}>Create Base notifications (30, 10, 5 seconds)</button>
            </div>
            <div>
              <h4>Breath Hold Notifications</h4>
              {this.renderBreathHoldNotifications()}
            </div>
            <div>
              <h4>Breathing Notifications</h4>
              {this.renderBreathingNotifications()}
            </div>
            <button onClick={this.clearNotifications}>Clear all notifications</button>
          </div>
          <button className="start-training-button" onClick={this.onSubmit}>Submit</button>
        </form>
      </>
    )
  }

  private addBreathHoldNotification = (notification: NotificationSettings) => {
    const breathHoldNotifications = this.updateNotifications(notification, this.state.breathHoldNotifications);
    this.setState({ ...this.state, breathHoldNotifications })
  };

  private addBreathingNotification = (notification: NotificationSettings) => {
    const breathingNotifications = this.updateNotifications(notification, this.state.breathingNotifications);
    this.setState({ ...this.state, breathingNotifications })
  };

  private updateNotifications = (notification: NotificationSettings, notifications: NotificationSettings[]) => {
    const currentNotifications = new Set(this.state.breathingNotifications);
    currentNotifications.add(notification);
    return Array.from(currentNotifications);
  };

  private renderBreathHoldNotifications() {
    return this.renderNotifications(this.state.breathHoldNotifications);
  }

  private renderBreathingNotifications() {
    return this.renderNotifications(this.state.breathingNotifications)
  }

  private renderNotifications(notifications: NotificationSettings[]) {
    if (notifications.length > 0) {
      return notifications.map((notification, index) => <p key={index}>{notification}</p>);
    } else {
      return <p>No notifications</p>;
    }
  }

  private createBaseNotifications = (event: SyntheticEvent) => {
    event.preventDefault();
    this.setState( { ...this.state, breathingNotifications: baseNotifications, breathHoldNotifications: baseNotifications });
  };

  private clearNotifications = (event: SyntheticEvent) => {
    event.preventDefault();
    this.setState( { ...this.state, breathingNotifications: [], breathHoldNotifications: [] });
  };

  private onSubmit = (event: SyntheticEvent) => {
    event.preventDefault();
    this.props.onSubmit(this.state);
  };

  private changeBreathHoldStartTime = (event: any) => {
    this.setState({...this.state, breathHoldStartTime: parseInt(event.target.value) });
  };

  private changeBreathHoldIncrement = (event: any) => {
    this.setState({...this.state, breathHoldIncrement: parseInt(event.target.value) });
  };

  private changeBreathingStartTime = (event: any) => {
    this.setState({...this.state, breathingStartTime: parseInt(event.target.value) });
  };

  private changeBreathingDecrement = (event: any) => {
    this.setState({...this.state, breathingDecrement: parseInt(event.target.value) });
  };

  private changeNumberOfReps = (event: any) => {
    this.setState({...this.state, numberOfReps: parseInt(event.target.value) });
  }
}

export default TimeTablesForm;
