const getAvailableVoice = () => {
  return window.speechSynthesis.getVoices().find(v => v.name === "Google UK English Male") ||
    window.speechSynthesis.getVoices().filter(voice => voice.lang === "en-US")[0];
};

const getUtter = (message: string, voice: SpeechSynthesisVoice) => {
  const utter = new SpeechSynthesisUtterance(message);
  utter.rate = 1;
  utter.pitch = 0.5;
  utter.voice = voice;
  return utter;
};

export function notifyUser(message: string) {
  const voice = getAvailableVoice();
  window.speechSynthesis.speak(getUtter(message, voice));
}
